<template>
    <v-container fill-height>
        <v-row justify="center" align="center">
            <v-col cols="5">
                <v-card>
                    <v-card-title class="d-flex justify-center primary--text text-h4 font-weight-bold">
                        Login
                    </v-card-title>
                    <v-container>
                        <v-row>
                            <v-col cols="12">
                                <v-text-field outlined dense hide-details label="Usuario"></v-text-field>
                            </v-col>
                            <v-col cols="12">
                                <v-text-field outlined dense hide-details label="Password" :type="showPassword ?'text':'password'" :append-icon="showPassword ? 'fas fa-eye':'fas fa-eye-slash'" @click:append="showPassword = !showPassword"></v-text-field>
                            </v-col>
                        </v-row>
                    </v-container>
                    <v-card-actions class="d-flex justify-center">
                        <v-btn depressed @click="initSession()" >
                            Sign In
                        </v-btn>
                    </v-card-actions>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
export default {
    data: () => {
        return {
            showPassword:false
        }
    },
    methods:{
        initSession(){
            this.$store.commit('createJwt')
            this.$router.push({name:'main'})
        }
    }
}
</script>